import React, {useState} from "react";
import { data } from "../utils/data";

export const NetworkTab = () => {
    const [isOpen, setIsOpen] = useState(false)
    return (
        <div className="">

            <div className="relative flex flex-col">
                <button onClick={()=>setIsOpen(!isOpen)} className="w-4/6 p-4 flex items-center bg-blue-400 justify-between font-bold tracking-wider border-4 border-transparent actie:border-white">Dropdown</button>
            </div>
            <ul className="absolute top-20 flex flex-col items-start rounded-lg p-2 w-4/6 ">
                {
                    isOpen && (
                        data.map((item,i)=>{
                            return(
                                <li
                                key={i} 
                                className="flex w-4/6 justify-between hover:bg-blue-300 cursor-pointer rounded-r-lg border-l-transparent hover:border-l-white border-l-4"
                                > 
                                <img 
                                src={item.icon}
                                alt="icon" 
                                height="50" 
                                width="50"/> {item.name} </li>
                            )
                        })
                    )
                }
                
                </ul> 


        </div>
    )
}



                       